.profile {
    display:block;
    max-width: 600px;
    height:650px;
    margin: 0 auto;
    padding-top: 25px;
    background-color: grey;

}
.description {
    display:block;
    margin: 0 auto 0;
    padding-top:20px;
    width: 400px;
    height:450px;
    border-top: solid rgb(152, 152, 156) 2px;
    border-right: solid rgb(152, 152, 156) 2px;
    border-left: solid rgb(152, 152, 156) 2px;
    border-radius: 3% 3% 0% 0%;
    background-color: rgb(246, 240, 240);
    text-align:center;
}
.avatar{
    display: block;
    margin: 0 auto;
    border-radius:50%;
}
.name {
    font-weight:700;
    font-size:24px;
}
.stats {
    display:flex;
    margin:0 auto;
    padding:0;
    width:400px;
    justify-content: space-evenly;
    background-color: lightgray;
    border: solid rgb(152, 152, 156) 2px;
    border-radius: 0% 0% 15px 15px;
}
.category {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    flex-basis: calc(100%  / 3);
    min-height:100px;
    text-align: center;
}
.category:nth-child(2) {
    border-right: solid rgb(152, 152, 156) 2px;
    border-left: solid rgb(152, 152, 156) 2px;
}

.label{
    display:block;
}
.quantity{
    font-weight:700;
}

